                font-family: 'Caslon';
                font-style: italic;
                text-transform: lowercase;
            }
            font-weight: normal;
            float: left;
            margin-bottom: 0;
            padding: 10px 80px 0;
        }
        input#rememberme {
            /* display: block; */
            margin: 0 auto;
            -webkit-appearance: none;
            border-radius: 100px;
            float: none;
            margin: 11px auto 11px;
            top: -3px;
            position: relative;
        }
        p.forgetmenot {
            display: block;
            width: 100%;
        }
        #backtoblog a,
        #nav a {
            color: white;
            font-family: 'Caslon';
            font-style: italic;
        }
    }
    input[type=submit]:hover {
        background: white;
        color: #f3553d;
        //        box-shadow: none !important;
